<template>
  <van-skeleton title :row="22" :loading="loading">
    <div class="categoryList">
      <van-nav-bar title left-text left-arrow @click-left="onClickLeft" fixed>
        <template
          #title
        >{{$route.query.name instanceof Object?$route.query.name.name:$route.query.name}}</template>
      </van-nav-bar>
      <ul v-if="listDetails" class="contentList">
        <li v-for="(item, index) in listDetails" :key="index">
          <BookList :first="item" />
        </li>
      </ul>
    </div>
  </van-skeleton>
</template>

<script>
// import { Toast } from 'vant';
import BookList from "@/components/BookList.vue";

export default {
  components: {
    BookList,
  },
  data() {
    return {
      listDetails: null,
      loading: true,
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
      this.listDetails = null;
    },
  },
  activated() {
    if (this.$route.query.active == 0) {
      this.axios
        .get(
          "https://novel.kele8.cn/category-info?gender=male&type=hot&major=" +
            this.$route.query.name +
            "&minor=&start=1&limit=20"
        )
        .then((response) => {
          this.listDetails = response.data.books;
          // console.log(this.listDetails);
        })
        .finally(() => {
          this.loading = false;
        });
    } else if (this.$route.query.active == 1) {
      this.axios
        .get(
          "https://novel.kele8.cn/category-info?gender=female&type=hot&major=" +
            this.$route.query.name +
            "&minor=&start=1&limit=20"
        )
        .then((response) => {
          this.listDetails = response.data.books;
          // console.log(this.listDetails);
        })
        .finally(() => {
          this.loading = false;
        });
    } else if (this.$route.query.active == 2) {
      this.axios
        .get(
          "https://novel.kele8.cn/category-info?gender=press&type=hot&major=" +
            this.$route.query.name +
            "&minor=&start=1&limit=20"
        )
        .then((response) => {
          this.listDetails = response.data.books;
          // console.log(this.listDetails);
        })
        .finally(() => {
          this.loading = false;
        });
    } else if (this.$route.query.active == 3) {
      this.axios
        .get("https://novel.kele8.cn/rank/" + this.$route.query.name.id)
        .then((response) => {
          this.listDetails = response.data.ranking.books;
        })
        .finally(() => {
          this.loading = false;
        });
    }
  },
};
</script>

<style lang="less">
.categoryList {
  .van-nav-bar {
    .van-nav-bar__content {
      .van-nav-bar__left {
        .van-icon {
          color: black;
          font-size: 20px;
        }
      }
    }
  }
}
.contentList {
  margin-top: 46px;
  margin-bottom: 50px;
}
</style>