<template>
  <div class="HomeBookListD">
    <h3>
      <slot></slot>
    </h3>
    <ul>
      <li
        class="booklist"
        v-for="(item, index) in currentHotSellRank"
        :key="index"
        @click="navigateToBookList(item._id)"
      >
        <div class="cover">
          <!-- <img :src="'http://statics.zhuishushenqi.com'+first.cover" alt /> -->
          <img :src="'http://statics.zhuishushenqi.com'+item.cover" alt />
        </div>
        <div class="right">
          <h4 class="name">{{item.title}}</h4>
          <p class="state">{{item.author}}</p>
        </div>
        <div class="round">{{index+1}}</div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["currentHotSellRank"],
  methods: {
    navigateToBookList: function (id) {
      this.$router.push({
        path: "/detail",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style lang="less" scoped>
.HomeBookListD {
  margin-top: 20px;
  margin-bottom: 20px;
  padding-left: 15px;
  position: relative;
  h3 {
    font-weight: bold;
  }
}
ul {
  &:after {
    content: "";
    height: 0;
    line-height: 0;
    display: block;
    visibility: hidden;
    clear: both;
  }
  padding-bottom: 10px;
  .booklist {
    position: relative;
    //   overflow: hidden;
    float: left;
    width: 50%;
    .cover {
      width: 68px;
      height: 88px;
      border-radius: 5px;
      float: left;
      margin-top: 15px;
      overflow: hidden;
    }
    .right {
      width: calc(100% - 88px);
      position: relative;
      height: 88px;
      float: left;
      margin-left: 10px;
      margin-right: 10px;
      margin-top: 15px;
      .name {
        font-size: 13px;
        font-weight: 400;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        text-overflow: ellipsis;
        // padding: 0 40px;
        margin-bottom: 10px;
      }
      .state {
        position: absolute;
        left: 0;
        bottom: 0;
        // margin-top: 40px;
        font-size: 12px;
        color: #999;
      }
    }
    .round {
      position: absolute;
      left: 55px;
      top: 90px;
      width: 20px;
      height: 20px;
      background: #5ab1f4;
      text-align: center;
      line-height: 20px;
      font-size: 12px;
      color: white;
      border-radius: 50%;
    }
    &:nth-of-type(1) {
      .round {
        background: #fcda75;
      }
    }
    &:nth-of-type(2) {
      .round {
        background: #d1ced1;
      }
    }
    &:nth-of-type(3) {
      .round {
        background: #f19162;
      }
    }
  }
}
</style>