import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    categories: null,
    chapter: null,
    issearch: true,
    word: null,
    searchResults: null,
    BookrackList: null,
    bookInfo: null,
    isShowBooksManagement: false,
  },
  mutations: {
    updateCategories: function (state, payload) {
      state.categories = payload
    },
    updateChapter: function (state, payload) {
      state.chapter = payload
    },
    updatebookInfo: function (state, payload) {
      state.bookInfo = payload
    },
    updateWord: function (state, payload) {
      state.word = payload
    },
    storageBookrack: function (state, payload) {
      state.BookrackList = payload
    },
    falseissearch: function (state) {
      state.issearch = false
    },
    trueissearch: function (state) {
      state.issearch = true
    },
    falseBooksManagement: function (state) {
      state.isShowBooksManagement = false
    },
    trueBooksManagement: function (state) {
      state.isShowBooksManagement = true
    },
    results: function (state, payload) {
      state.searchResults = payload
    },
  },
  actions: {
    getResults({ commit },searchTop) {
      window.axios.get("http://novel.kele8.cn/search?keyword="+searchTop.value)
        .then(response => {
          commit("results", response.data.books);
        })
        .catch((error) => {
          console.log(error)
        })
    }
  },
  modules: {
  }
})
