<template>
  <div class="search">
    <SearchTop />
    <!-- aaa=$event -->
    <template v-if="issearch">
      <van-skeleton title :row="3" :loading="loading">
        <HotSearchBooks v-if="hotwords" :currenthotwords="currenthotwords" />
      </van-skeleton>
    </template>
    <template v-else>
      <SearchResults />
    </template>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { mapMutations } from "vuex";

import SearchTop from "@/components/SearchTop.vue";
import HotSearchBooks from "@/components/HotSearchBooks.vue";
import SearchResults from "@/components/SearchResults.vue";

export default {
  components: {
    SearchTop,
    HotSearchBooks,
    SearchResults,
  },
  data() {
    return {
      hotwords: null,
      hotwordsindex: 0,
      aaa: null,
      loading: true,
    };
  },
  created() {
    this.axios
      .get("https://novel.kele8.cn/search-hotwords")
      .then((res) => {
        this.hotwords = res.data.searchHotWords;
        // console.log(this.hotwords);
      })
      .finally(() => {
        this.loading = false;
      });
  },
  computed: {
    currenthotwords: function () {
      return this.hotwords.slice(
        this.hotwordsindex * 10,
        (this.hotwordsindex + 1) * 10
      );
    },
    ...mapState(["issearch"]),
  },
  activated() {
    this.hotwordsindex = this.hotwordsindex >= 10 ? 0 : this.hotwordsindex + 1;
  },
  methods: {
    ...mapMutations(["falseissearch", "trueissearch"]),
  },
};
</script>

<style>
.van-skeleton{
  margin: 50px 0 0 0;
}
</style>