<template>
  <van-skeleton title :row="3" :loading="loading">
    <div
      class="book"
      :class="[act==1?'bookClass':(act==2?'bookNight':'bookEyecare')]"
      id="mochu"
      ref="testDom"
    >
      <van-overlay :show="overlay" @click="overlay = false">
        <div class="wrapper" @click.stop>
          <van-loading color="#1989fa">加载中...</van-loading>
        </div>
      </van-overlay>
      <van-popup
        v-if="bookInfo"
        v-model="show"
        position="left"
        :style="{ height: '100%',width:'80%' }"
      >
        <div class="popupTitle">{{bookInfo.title}}</div>
        <div class="directory">目录</div>
        <div class="popupDirectoryBox">
          <ul class="popupDirectory" :style="{top:(-$route.query.chapter_index*41.8)+'px'}">
            <li
              v-for="(item, index) in bookchapter"
              :key="index"
              @click="popup(index,$route.query.id)"
            >{{item.title}}</li>
          </ul>
        </div>
      </van-popup>
      <template v-if="isPopupWindow">
        <!-- <transition name="van-fade"> -->
        <van-nav-bar
          v-if="bookInfo"
          :left-text="bookInfo.title"
          right-text="目录"
          left-arrow
          @click-left="onClickLeft"
          @click-right="onClickRight"
          fixed
        />
        <!-- </transition> -->

        <!-- <transition name="van-fade"> -->
        <div class="PopupWindowBottom">
          <div class="item">
            <span class="font-btn-w" @click="onFontSmaller">A-</span>
            <span class="bar">{{fontsize}}</span>
            <span class="font-btn-w" @click="onFontBigger">A+</span>
            <span class="default" @click="onDefault">默认</span>
          </div>
          <div class="item">
            <span class="square" :class="{active:act==1}" @click="classDefault">默认</span>
            <span class="square" :class="{active:act==2}" @click="classNight">夜间</span>
            <span class="square" :class="{active:act==3}" @click="classEyecare">护眼</span>
          </div>
          <div class="item">
            <div class="previousChapter" @click="previousChapter">上一章</div>
            <div class="progressBar">
              <van-slider v-model="value" @change="onChange" button-size="13px" />
            </div>
            <div class="nextChapter" @click="nextChapter">下一章</div>
          </div>
        </div>
        <!-- </transition> -->
      </template>
      <div class="bookContent" v-if="content" @click="PopupWindow">
        <h1 :style="{fontSize:(fontsize+10)+'px'}">{{ content.title }}</h1>
        <div
          style="white-space: pre-wrap;"
          :style="{fontSize:fontsize+'px'}"
        >{{ content.cpContent }}</div>
      </div>
    </div>
  </van-skeleton>
</template>

<script>
import { Toast } from "vant";
import { mapState } from "vuex";

export default {
  data() {
    return {
      content: null,
      bookchapter: null,
      isPopupWindow: false,
      count: 1,
      loading: false,
      value: 0,
      fontsize: 16,
      act: 1,
      show: false,
      overlay: true,
      skip_index: 0,
      skipParse: null,
      skipStringify: null,
      parse: null,
      stringify: null,
    };
  },
  created() {
    this.skip_index = this.$route.query.chapter_index;
  },
  computed: {
    ...mapState(["chapter", "bookInfo"]),
  },
  updated() {
    let _this = this;
    window.addEventListener("scroll", function () {
      let top =
        document.documentElement.scrollTop ||
        document.body.scrollTop ||
        window.pageYOffset;
      //要做兼容 在模拟器能正常获取scrolltop在微信h5页面和手机的浏览器页面一直为0
      let h = _this.$refs.testDom.clientHeight;
      let wh = window.innerHeight;
      if (Math.ceil(wh + top) >= h) {
        if (_this.$route.query.chapter_index) {
          document.body.scrollTop = 0;
          document.documentElement.scrollTop = 0;
          _this.navigateToBook(
            Number(_this.$route.query.chapter_index) + 1,
            _this.$route.query.id
          );
          Toast({
            message: "下一章",
            position: "top",
          });
          _this.skip(_this.$route.query.chapter_index);
        }
      }
      if (Math.ceil(top) > h - wh) {
        top = top - 1;
      }
      _this.value = Math.round((100 * top) / (h - wh));
      // console.log(Math.ceil(top));
      // console.log(h);
      // console.log(h - wh);
    });
  },
  methods: {
    getcontent: function (index) {
      this.overlay = true;
      this.axios
        .get(
          "https://novel.kele8.cn/chapters/" +
            encodeURIComponent(this.bookchapter[index].link)
        )
        .then((response) => {
          this.content = response.data.chapter;
        })
        .finally(() => {
          this.overlay = false;
        });
    },
    PopupWindow: function () {
      this.isPopupWindow = !this.isPopupWindow;
    },
    navigateToBook(chapter_index, id) {
      this.$router.push({
        path: "/book",
        query: {
          chapter_index,
          id,
        },
      });
    },
    popup: function (index, id) {
      this.navigateToBook(index, id);
      this.show = false;
      this.skip(this.$route.query.chapter_index);
    },
    onClickLeft() {
      this.$router.go(-this.count);
      this.count = 0;
    },
    onClickRight() {
      this.show = true;
      this.isPopupWindow = false;
    },
    onChange(value) {
      let h = this.$refs.testDom.clientHeight;
      let wh = window.innerHeight;
      console.log("当前值：" + value);
      console.log(h - wh);
      document.documentElement.scrollTop = (value * (h - wh)) / 100;
    },
    onFontSmaller() {
      if (this.fontsize > 12) {
        this.fontsize--;
      } else {
        Toast({
          message: "字体最小了",
          position: "top",
        });
      }
    },
    onFontBigger() {
      if (this.fontsize < 30) {
        this.fontsize++;
      } else {
        Toast({
          message: "字体最大了",
          position: "top",
        });
      }
    },
    onDefault() {
      this.fontsize = 16;
    },
    classDefault() {
      this.act = 1;
    },
    classNight() {
      this.act = 2;
    },
    classEyecare() {
      this.act = 3;
    },
    previousChapter() {
      this.isPopupWindow = false;
      if (this.$route.query.chapter_index >= 1) {
        this.navigateToBook(
          Number(this.$route.query.chapter_index) - 1,
          this.$route.query.id
        );
        this.skip(this.$route.query.chapter_index);
      } else {
        Toast({
          message: "没有上一章了",
          position: "top",
        });
      }
    },
    nextChapter() {
      this.isPopupWindow = false;
      this.$route.query.id;
      this.navigateToBook(
        Number(this.$route.query.chapter_index) + 1,
        this.$route.query.id
      );
      this.skip(this.$route.query.chapter_index);
    },
    skip(skip_index) {
      console.log(skip_index);
      this.skipParse = localStorage.getItem("ChapterRecord");
      this.skipParse = JSON.parse(this.skipParse);
      for (let i = 0; i < this.skipParse.length; i++) {
        if (this.skipParse[i].id === this.$route.query.id) {
          // this.skipParse[i] = {
          //   id: this.$route.query.id,
          //   index: this.skipParse,
          // };
          this.skipParse[i].index = skip_index;
          this.count++;
        }
      }
      this.skipStringify = JSON.stringify(this.skipParse);
      localStorage.setItem("ChapterRecord", this.skipStringify);
      // console.log(this.chapter_index);

      this.parse = localStorage.getItem("Bookrack");
      this.parse = JSON.parse(this.parse);
      for (let i = 0; i < this.parse.length; i++) {
        if (this.parse[i].id === this.$route.query.id) {
          // this.skipParse[i] = {
          //   id: this.$route.query.id,
          //   index: this.skipParse,
          // };
          this.parse[i].index = skip_index;
        }
      }
      this.stringify = JSON.stringify(this.parse);
      localStorage.setItem("Bookrack", this.stringify);
      this.$store.commit("storageBookrack", this.stringify);
    },
  },
  activated() {
    this.isPopupWindow = false;
    this.bookchapter = this.chapter;
    if (!this.chapter) {
      this.axios
        .get(
          "https://novel.kele8.cn/book-sources?view=summary&book=" +
            this.$route.query.id
        )
        .then((res) => {
          // 根据书籍源获取 章节
          // console.log(res.data[0]._id);
          this.axios
            .get("https://novel.kele8.cn/book-chapters/" + res.data[0]._id)
            .then((res) => {
              // console.log(res.data);
              this.$store.commit("updateChapter", res.data.chapters);
              this.bookchapter = res.data.chapters;
              this.getcontent(this.$route.query.chapter_index);
              // this.getcontent();
            });
        });
    } else this.getcontent(this.$route.query.chapter_index);
  },
  watch: {
    "$route.query.chapter_index": function () {
      if (this.$route.query.chapter_index) {
        this.skip_index = this.$route.query.chapter_index;
        this.count++;
        // this.overlay = true;
        this.getcontent(this.$route.query.chapter_index);
        // this.overlay = false;
      }
    },
  },
};
</script>

<style lang="less">
.book {
  .van-overlay {
    background-color: rgba(0, 0, 0, 0.9);
    .wrapper {
      display: flex;
      // align-items: center;
      margin-top: 50px;
      justify-content: center;
      height: 100%;
      .van-loading {
        .van-loading__text {
          color: #1989fa;
        }
      }
    }
  }
  background: #eee6dd;
  padding-top: 15px;
  .bookContent {
    & > h1 {
      font-size: 26px;
      margin-bottom: 15px;
    }
  }
  .van-nav-bar {
    .van-nav-bar__content {
      background-color: rgba(0, 0, 0, 0.7);
      .van-nav-bar__left {
        .van-icon-arrow-left {
          color: white;
        }
        .van-nav-bar__text {
          color: white;
          display: inline-block;
          font-size: 14px;
          width: 100px;
          text-align: left;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          word-break: break-all;
          text-overflow: ellipsis;
          overflow: hidden;
        }
      }
      .van-nav-bar__right {
        & > .van-nav-bar__text {
          color: white;
          font-size: 14px;
        }
      }
    }
  }
  .PopupWindowBottom {
    width: 100vw;
    height: 155px;
    background-color: rgba(0, 0, 0, 0.7);
    position: fixed;
    bottom: 0;
    left: 0;
    color: white;
    .item {
      display: flex;
      &:nth-of-type(1) {
        margin: 15px 0;
      }
      &:nth-of-type(2) {
        margin: 20px 0 5px 0;
      }
      .font-btn-w {
        width: 28%;
        height: 30px;
        line-height: 30px;
        text-align: center;
        margin: 0 15px;
        border: 1px solid #fff;
        border-radius: 15px;
      }
      .bar {
        line-height: 30px;
      }
      .default {
        width: 14%;
        height: 30px;
        text-align: center;
        line-height: 30px;
        margin-left: 10px;
        border: 1px solid #46a9fcde;
        border-radius: 15px;
        font-size: 14px;
      }
      .square {
        // flex: auto;
        width: 28%;
        height: 28px;
        line-height: 28px;
        text-align: center;
        color: #fff;
        border: 1px solid #999;
        border-radius: 3px;
        &:nth-of-type(1) {
          margin-left: 15px;
        }
        &:nth-of-type(2) {
          margin: 0 15px;
        }
      }
      .active {
        border: 1px solid #46a9fcde;
        color: #46a9fcde;
      }
      .previousChapter {
        width: 20%;
        height: 50px;
        line-height: 50px;
        text-align: center;
      }
      .progressBar {
        flex: 1;
        padding-top: 24px;
        // padding-left: 10px;
        text-align: center;
        .van-slider {
          width: 90%;
          margin: 0 5%;
        }
      }
      .nextChapter {
        width: 20%;
        height: 50px;
        line-height: 50px;
        text-align: center;
      }
    }
  }
  .van-popup {
    padding-top: 12px;
    padding-left: 15px;
    // overflow: hidden;
    .popupTitle {
      font-size: 20px;
    }
    .directory {
      font-size: 18px;
      color: #a2a2a2;
      padding: 10px 0;
      border-bottom: 1px solid #f2f2f2;
    }
    .popupDirectoryBox {
      overflow: hidden;
      .popupDirectory {
        position: relative;
        top: 0;
        & > li {
          padding: 10px 0;
          color: #a2a2a2;
          border-bottom: 1px solid #f2f2f2;
        }
      }
    }
  }
}
.bookNight {
  background: #0c0c0c;
  color: #888;
}
.bookClass {
  background: #eee6dd;
}
.bookEyecare {
  background: #b8cd8d;
  color: #333;
}
</style>