<template>
  <div class="Detail" ref="testDom">
    <van-nav-bar title fixed left-text left-arrow :border="false" @click-left="onClickLeft">
      <template #title>{{name}}</template>
    </van-nav-bar>
    <DetailDescribe :bookInfo="bookInfo" />
    <van-tabs v-model="active" animated>
      <van-tab class="details" title="详情">
        <div class="instructions" v-if="bookInfo">
          <p>{{bookInfo.longIntro}}</p>
        </div>
        <div class="related">
          <div class="title">
            <h3>同类热门推荐</h3>
            <div class="right"></div>
          </div>
          <div class="recommendList" v-if="recommend">
            <BookList v-for="(item, index) in updataRecommend" :key="index" :first="item" />
          </div>
        </div>
        <div class="tips" v-if="bookInfo">
          <div>{{bookInfo.wordCount?bookInfo.wordCount>10000?("字数:"+Math.round(bookInfo.wordCount/10000)+"万字"):bookInfo.wordCount:("集数:"+bookInfo.chaptersCount+"话")}}</div>
          <div>本书数字版权由第三方提供并发行，如有任何疑问请通过 我的-意见反馈 告知我们。</div>
        </div>
      </van-tab>
      <van-tab title="目录">
        <div class="directorynav" v-if="directory">
          <div
            class="navtext"
          >{{bookInfo.isSerial?("连载中至"+directory.length+"章.更新于"+bookInfo.updated.slice(0, 10)):("共"+directory.length+"章 已完结")}}</div>
          <div class="right">
            <svg
              t="1610346450309"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="3819"
              width="16"
              height="16"
            >
              <path
                d="M426.496 926.208V97.792c0-24.064-19.456-43.52-43.52-43.52-13.312 0-25.6 6.144-33.792 16.384-3.584 2.56-6.144 5.632-8.704 9.216l-217.088 309.76c-13.824 19.456-9.216 46.592 10.24 60.416s46.592 9.216 60.416-10.24l145.408-207.36v693.76c0 24.064 19.456 43.52 43.52 43.52s43.52-19.968 43.52-43.52zM599.552 97.792v827.904c0 24.064 19.456 43.52 43.52 43.52 13.312 0 25.6-6.144 33.792-16.384 3.072-2.56 6.144-5.632 8.704-9.216l217.088-309.248c13.824-19.456 9.216-46.592-10.752-60.416-19.456-13.824-46.592-9.216-60.416 10.752L686.08 792.064V97.792c0-24.064-19.456-43.52-43.52-43.52s-43.008 19.968-43.008 43.52z"
                p-id="3820"
                fill="#515151"
              />
            </svg>
            {{order}}
          </div>
        </div>
        <ul class="directorydetails">
          <li
            v-for="(item, index) in directory"
            :key="index"
            @click="navigateToBook(index,$route.query.id)"
          >{{item.title}}</li>
        </ul>
      </van-tab>
    </van-tabs>
    <div class="theconsole">
      <div class="addbookcase" @click="AddBookcase">
        <svg
          t="1610332495434"
          class="icon"
          viewBox="0 0 1211 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="2397"
          width="22"
          height="22"
        >
          <path
            d="M896.556 358.601h38.322v38.322a38.433 38.433 0 0 0 38.321 38.322 38.433 38.433 0 0 0 38.322-38.322V358.6h38.322a38.433 38.433 0 0 0 38.434-38.433 38.433 38.433 0 0 0-38.322-38.322h-38.322v-38.322a38.433 38.433 0 0 0-38.322-38.322 38.433 38.433 0 0 0-38.322 38.322v38.322h-38.433a38.433 38.433 0 0 0-38.322 38.322 38.433 38.433 0 0 0 38.322 38.433z"
            p-id="2398"
            fill="#43474A"
          />
          <path
            d="M1050.289 857.12h-38.656V550.433a38.433 38.433 0 0 0-38.322-38.322 38.433 38.433 0 0 0-38.322 38.322V857.12h-153.51V206.873c0-64.278-52.582-116.748-116.748-116.748h-149.5c-64.279 0-116.748 52.581-116.748 116.748V857.12h-154.18V703.832h38.657a38.433 38.433 0 0 0 38.322-38.322 38.433 38.433 0 0 0-38.322-38.322h-38.656V511.666a37.43 37.43 0 0 1 37.32-37.32h39.77v-77.2h-36.429c-64.278 0-116.748 52.58-116.748 116.748V857.12h-39.659a38.433 38.433 0 0 0-38.322 38.322 38.433 38.433 0 0 0 38.322 38.322h921.73a38.433 38.433 0 0 0 38.323-38.322 38.433 38.433 0 0 0-38.322-38.322zM474.68 204.645A37.43 37.43 0 0 1 512 167.325h155.516a37.43 37.43 0 0 1 37.207 37.32V857.12H474.681z"
            p-id="2399"
            fill="#43474A"
          />
        </svg>
        <div class="text">加入书架</div>
      </div>
      <div class="reading" @click="navigateToBook(chapter_index,$route.query.id)">
        <div>开始阅读</div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import DetailDescribe from "@/components/DetailDescribe.vue";
import BookList from "@/components/BookList.vue";

export default {
  components: {
    DetailDescribe,
    BookList,
  },
  data() {
    return {
      bookInfo: null,
      recommend: null,
      name: " ",
      active: 0,
      directory: null,
      order: "倒序",
      chapter_index: 0,
      count: 0,
      parse: null,
      stringify: null,
      chapterRecordParse: null,
      chapterRecordStringify: null,
      record_chapter_index: null,
    };
  },
  created() {
    this.record_chapter_index = localStorage.getItem("ChapterRecord");
    this.record_chapter_index = JSON.parse(this.record_chapter_index);
    for (let i = 0; i < this.record_chapter_index.length; i++) {
      if (this.record_chapter_index[i].id === this.$route.query.id) {
        // this.chapter_index=
        this.chapter_index = this.record_chapter_index[i].index;
      }
    }
    // this.chapter_index = 9;
    // document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
      this.listDetails = null;
      this.directory = null;
      this.active = 0;
      this.count = 0;
      // this.chapter_index=0;
    },
    navigateToBook(chapter_index, id) {
      this.$router.push({
        path: "/book",
        query: {
          chapter_index,
          id,
        },
      });
      this.chapter_index = chapter_index;
      this.getChapterRecord(this.chapter_index);
      this.setChapterRecord();
    },
    getChapterRecord: function (chapter_index) {
      // console.log("获取了章节");
      this.chapterRecordParse = localStorage.getItem("ChapterRecord");
      this.chapterRecordParse = JSON.parse(this.chapterRecordParse);

      if (this.chapterRecordParse.length == 0) {
        this.chapterRecordParse.push({
          id: this.$route.query.id,
          index: chapter_index,
        });
      } else {
        this.chapterRecordParse.forEach((element) => {
          if (element.id == this.$route.query.id) {
            this.count++;
          }
        });
        if (this.count == 0) {
          this.chapterRecordParse.push({
            id: this.$route.query.id,
            index: chapter_index,
          });
        }
      }

      // console.log(this.chapterRecordParse);
    },
    setChapterRecord: function () {
      // console.log("记录了章节");
      this.chapterRecordStringify = JSON.stringify(this.chapterRecordParse);
      localStorage.setItem("ChapterRecord", this.chapterRecordStringify);
      // console.log(this.chapterRecordStringify);
      // console.log("111");
    },
    getItem: function (chapter_index) {
      this.parse = localStorage.getItem("Bookrack");
      this.parse = JSON.parse(this.parse);
      if (this.parse.length == 0) {
        this.parse.push({
          id: this.$route.query.id,
          index: chapter_index,
          title: this.bookInfo.title,
          chaptersCount: this.bookInfo.chaptersCount,
          cover: this.bookInfo.cover,
        });
      } else {
        this.parse.forEach((element) => {
          if (element.id == this.$route.query.id) {
            this.count++;
          }
        });
        if (this.count == 0) {
          this.parse.push({
            id: this.$route.query.id,
            index: chapter_index,
            title: this.bookInfo.title,
            chaptersCount: this.bookInfo.chaptersCount,
            cover: this.bookInfo.cover,
          });
        }
      }
      // console.log(this.parse);
    },
    setItem: function () {
      this.stringify = JSON.stringify(this.parse);
      localStorage.setItem("Bookrack", this.stringify);
      // console.log(this.stringify);
      // console.log("111");
    },
    AddBookcase: function () {
      this.getItem(this.chapter_index);
      this.setItem();
      this.$store.commit("storageBookrack", this.stringify);
    },
  },
  updated() {
    let _this = this;
    window.addEventListener("scroll", function () {
      let top =
        document.documentElement.scrollTop ||
        document.body.scrollTop ||
        window.pageYOffset;
      //要做兼容 在模拟器能正常获取scrolltop在微信h5页面和手机的浏览器页面一直为0
      // let h = _this.$refs.testDom.clientHeight;
      let wh = window.innerHeight;
      // console.log(Math.ceil(wh + top));
      // console.log(h);
      if (Math.ceil(wh + top) >= 820) {
        _this.name = _this.bookInfo.title;
      } else {
        _this.name = " ";
      }
    });
  },
  beforeRouteEnter(to, from, next) {
    if (from.name == "Home") {
      Toast.loading({
        message: "加载中...",
        forbidClick: false,
      });
    }
    window.axios
      .get("https://novel.kele8.cn/book-info/" + to.query.id)
      .then((response) => {
        next((vm) => {
          vm.bookInfo = response.data;
          vm.$store.commit("updatebookInfo", response.data);
          if (!vm.bookInfo) {
            Toast.loading({
              message: "加载中...",
              forbidClick: false,
            });
          }
          // console.log(vm.$store);
        });
      });
    // console.log(to, from, next);
  },
  watch: {
    bookInfo: function () {
      this.axios
        .get("https://novel.kele8.cn/recommend/" + this.$route.query.id)
        .then((response) => {
          this.recommend = response.data.books;
        });
      // if (!this.directory) {
      this.axios
        .get(
          "https://novel.kele8.cn/book-sources?view=summary&book=" +
            this.$route.query.id
        )
        .then((res) => {
          // 根据书籍源获取 章节
          // console.log(res.data[0]._id);
          this.axios
            .get("https://novel.kele8.cn/book-chapters/" + res.data[0]._id)
            .then((res) => {
              // console.log(res.data);
              this.$store.commit("updateChapter", res.data.chapters);
              this.directory = res.data.chapters;
            });
        });
      // }
    },
    "$route.query.id": function () {
      if (this.$route.query.id) {
        Toast.loading({
          message: "加载中...",
          forbidClick: true,
        });
        this.axios
          .get("https://novel.kele8.cn/book-info/" + this.$route.query.id)
          .then((response) => {
            this.bookInfo = response.data;
            this.$store.commit("updatebookInfo", response.data);
            if (this.bookInfo) {
              Toast.loading({
                message: "加载中...",
                forbidClick: false,
              });
            }
          });
      }
    },
    chapter_index: function () {
      this.chapterRecordParse = localStorage.getItem("ChapterRecord");
      this.chapterRecordParse = JSON.parse(this.chapterRecordParse);
      for (let i = 0; i < this.chapterRecordParse.length; i++) {
        if (this.chapterRecordParse[i].id === this.$route.query.id) {
          // this.chapterRecordParse[i] = {
          //   id: this.$route.query.id,
          //   index: this.chapterRecordParse,
          // };
          this.chapterRecordParse[i].index = this.chapter_index;
          this.count++;
        }
      }
      this.chapterRecordStringify = JSON.stringify(this.chapterRecordParse);
      localStorage.setItem("ChapterRecord", this.chapterRecordStringify);
      // console.log(this.chapter_index);

      this.parse = localStorage.getItem("Bookrack");
      this.parse = JSON.parse(this.parse);
      for (let i = 0; i < this.parse.length; i++) {
        if (this.parse[i].id === this.$route.query.id) {
          // this.chapterRecordParse[i] = {
          //   id: this.$route.query.id,
          //   index: this.chapterRecordParse,
          // };
          this.parse[i].index = this.chapter_index;
        }
      }
      this.stringify = JSON.stringify(this.parse);
      localStorage.setItem("Bookrack", this.stringify);
      this.$store.commit("storageBookrack", this.stringify);
    },
  },
  computed: {
    updataRecommend: function () {
      return this.recommend.slice(0, 10);
    },
  },
  activated() {
    this.record_chapter_index = localStorage.getItem("ChapterRecord");
    this.record_chapter_index = JSON.parse(this.record_chapter_index);
    for (let i = 0; i < this.record_chapter_index.length; i++) {
      if (this.record_chapter_index[i].id === this.$route.query.id) {
        // this.chapter_index=
        this.chapter_index = this.record_chapter_index[i].index;
        // console.log(this.record_chapter_index[i].index);
      } else {
        // this.chapter_index = 0;
      }
    }
    document.documentElement.scrollTop = 0;
  },
};
</script>

<style lang="less" >
.Detail {
  .van-nav-bar {
    background: #586a7e;
    .van-nav-bar__content {
      .van-nav-bar__left {
        .van-icon {
          color: white;
          font-size: 20px;
        }
      }
      .van-nav-bar__title {
        color: white;
      }
    }
  }
  .van-tabs {
    margin-top: -20px;
    .van-tabs__wrap {
      border-radius: 10px 10px 0 0;
      .van-tabs__nav {
        .van-tab {
          color: #323233;
          .van-tab__text {
            font-weight: 700;
          }
        }
        .van-tabs__line {
          width: 20px;
          background-color: #46a9fc;
          z-index: 0;
        }
      }
    }
    .van-tabs__content {
      background-color: #f7f5f6;
      margin-top: 20px;
      margin-bottom: 50px;
      .van-tab__pane {
        .instructions {
          background: #fff;
          padding-bottom: 10px;
          p {
            padding: 0 10px;
            font-size: 13px;
            color: #323233;
            font-weight: 550;
            display: -webkit-box;
            -webkit-line-clamp: 4;
            -webkit-box-orient: vertical;
            word-break: break-all;
            text-overflow: ellipsis;
            overflow: hidden;
          }
        }
        .related {
          background: #fff;
          padding-bottom: 20px;
          .title {
            padding: 0 10px;
            margin: 15px 0 0 0;
          }
        }
        .tips {
          margin-top: 10px;
          background: #fff;
          color: #9d9d9d;
          padding: 10px;
          font-size: 14px;
        }
        .directorynav {
          position: relative;
          background: white;
          .navtext {
            background: #f4f8fb;
            height: 30px;
            border-radius: 15px;
            margin: 0 15px;
            padding: 0 10px;
            line-height: 30px;
            font-size: 12px;
          }
          & > .right {
            position: absolute;
            top: 0;
            right: 25px;
            vertical-align: middle;
            font-size: 14px;
            line-height: 30px;
            & > svg {
              vertical-align: middle;
            }
          }
        }
        .directorydetails {
          padding-top: 25px;
          padding-left: 15px;
          background-color: white;
          & > li {
            padding: 10px 0;
            font-size: 14px;
          }
          & > li:nth-of-type(1) {
            color: #46a9fc;
          }
        }
      }
    }
  }
  .theconsole {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 50px;
    background: white;
    display: flex;
    .addbookcase {
      width: 40vw;
      text-align: center;
      padding-top: 5px;
      .text {
        font-size: 12px;
        font-weight: 600;
      }
    }
    .reading {
      width: 60vw;
      text-align: center;
      padding-top: 8px;
      & > div {
        display: inline-block;
        width: 80%;
        height: 35px;
        background: #46a9fc;
        line-height: 35px;
        color: white;
        border-radius: 20px;
      }
    }
  }
}
</style>