<template>
  <div class="searchtop">
    <van-nav-bar  title left-text fixed left-arrow @click-left="onClickLeft" >
      <template #right>
        <!-- <van-icon name="search" size="18" /> -->
        <van-search
          v-model="value"
          shape="round"
          placeholder="搜索作者、书名、分类"
          left-icon="nane"
          background="#F6F7FB"
          :clearable="false"
          @search="onSearch"
        />
        <van-icon class="myicon" name="search" size="20" @click="onSearch" />
      </template>
    </van-nav-bar>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import { mapState } from "vuex";
import { mapActions } from "vuex";
export default {
  data() {
    return {
      value: "",
    };
  },
  computed: {
    ...mapState(["word"]),
  },
  methods: {
    ...mapMutations(["falseissearch", "trueissearch"]),
    ...mapActions(["results"]),
    onSearch() {
      if (this.value) {
        this.falseissearch();
        this.$store.commit("updateWord", this.value);
        this.$store.dispatch("getResults", {
          value: this.value,
        });
        // this.$emit("update:searchResults", this.searchResults)
      }
    },
    onClickLeft() {
      this.$router.go(-1);
      this.value = "";
      this.trueissearch();
    },
  },
  watch: {
    value: function () {
      if (!this.value) {
        this.trueissearch();
        this.$store.commit("updateWord", null);
      }
    },
    word: function () {
      this.value = this.word;
      this.falseissearch();
      if (this.value) {
        this.$store.dispatch("getResults", {
          value: this.value,
        });
      }
    },
  },
};
</script>

<style lang="less">
.searchtop {
  .van-nav-bar {
    .van-icon {
      color: black;
    }
    .van-nav-bar__content {
      background: #f6f7fb;
      .van-nav-bar__left {
        padding: 0;
        padding-left: 8px;
        & > .van-icon {
          font-size: 20px;
        }
      }
      .van-nav-bar__right {
        padding: 0;
        .van-search {
          padding: 10px 12px 10px 0px;
          .van-search__content {
            width: 315px;
            background: #fff;
            .van-cell {
              padding: 0 8px 0 0;
              .van-cell__value {
                .van-field__body {
                  height: 30px;
                  .van-field__right-icon {
                    padding: 0 8px 0 8px;
                    .van-icon {
                      font-size: 20px;
                    }
                  }
                }
              }
            }
          }
        }
        .myicon {
          position: absolute;
          top: 12px;
          right: 25px;
        }
      }
    }
  }
}
input[type="search"]::-webkit-search-cancel-button {
  display: none;
}
</style>