<template>
  <div class="CategoryContent">
    <van-tabs
      v-model="active"
      animated
      line-width="0px"
      background="#F6F7FB"
      title-active-color="black"
    >
      <van-tab title="男频">
        <TabContent v-if="categories" :categories="categories.male" :icon="maleIcon" :active="active"/>
      </van-tab>
      <van-tab title="女频">
        <TabContent v-if="categories" :categories="sliceCategoriesFemale" :icon="femaleIcon" :active="active"/>
      </van-tab>
      <van-tab title disabled></van-tab>
    </van-tabs>
  </div>
</template>

<script>
import TabContent from "@/components/TabContent.vue";
import { mapState } from "vuex";

export default {
  data() {
    return {
      active: 0,
      maleIcon: [
        {imgSrc: require("../assets/Category/icon01.png")},
        {imgSrc: require("../assets/Category/icon02.png")},
        {imgSrc: require("../assets/Category/icon03.png")},
        {imgSrc: require("../assets/Category/icon04.png")},
        {imgSrc: require("../assets/Category/icon05.png")},
        {imgSrc: require("../assets/Category/icon06.png")},
        {imgSrc: require("../assets/Category/icon07.png")},
        {imgSrc: require("../assets/Category/icon08.png")},
        {imgSrc: require("../assets/Category/icon09.png")},
        {imgSrc: require("../assets/Category/icon10.png")},
        {imgSrc: require("../assets/Category/icon11.png")},
        {imgSrc: require("../assets/Category/icon12.png")},
        {imgSrc: require("../assets/Category/icon13.png")},
        {imgSrc: require("../assets/Category/icon14.png")},
      ],
      femaleIcon: [
        {imgSrc: require("../assets/Category/icon15.png")},
        {imgSrc: require("../assets/Category/icon16.png")},
        {imgSrc: require("../assets/Category/icon17.png")},
        {imgSrc: require("../assets/Category/icon19.png")},
        {imgSrc: require("../assets/Category/icon04.png")},
        {imgSrc: require("../assets/Category/icon11.png")},
        {imgSrc: require("../assets/Category/icon22.png")},
        {imgSrc: require("../assets/Category/icon13.png")},
        {imgSrc: require("../assets/Category/icon20.png")},
        // "/Category/icon15.png",
        // "/Category/icon16.png",
        // "/Category/icon17.png",
        // "/Category/icon19.png",
        // "/Category/icon04.png",
        // "/Category/icon11.png",
        // "/Category/icon22.png",
        // "/Category/icon13.png",
        // "/Category/icon20.png",
      ],
      ccc:null
    };
  },
  computed: {
    ...mapState(["categories"]),
    sliceCategoriesFemale:function () {
      return this.categories.female.slice(0,3).concat(this.categories.female.slice(4,7)).concat(this.categories.female.slice(8,11))
      // return this.categories.female.slice(8,11)
    }
  },
  methods: {},
  components: {
    TabContent,
  },
};
</script>

<style lang="less" >
.CategoryContent {
  .van-tabs {
    .van-tabs__wrap {
      .van-tabs__nav {
        //   background: #F6F7FB;
        .van-tab {
          color: #ccc !important;
          background: white;
          border-radius: 10px 10px 0 0;
          box-shadow: 0px 0px 2px #ccc;
          &:nth-of-type(3) {
            background: #f6f7fb;
            box-shadow: none;
          }
          &:nth-of-type(1) {
            margin: 0 10px;
          }
        }
        .van-tab--active {
          color: black !important;
        }
      }
    }
    .van-tabs__content {
      background: white;
      width: 95%;
      // height: 70vh;
      margin-left: 10px;
      border-radius: 0 10px 0 0;
      box-shadow: 0px 0px 5px #ccc;
    }
  }
}
</style>