<template>
  <van-skeleton title :row="20" :loading="loading">
    <div class="searchresults">
      <ul v-if="searchResults">
        <li v-for="(item, index) in searchResults" :key="index">
          <BookList :first="item" />
        </li>
      </ul>
      <div class="show-more">没有更多书籍</div>
    </div>
  </van-skeleton>
</template>

<script>
import BookList from "@/components/BookList.vue";
import { mapState } from "vuex";
export default {
  data() {
    return {
      loading: true,
    };
  },
  components: {
    BookList,
  },
  computed: {
    ...mapState(["searchResults"]),
  },
  watch: {
    searchResults() {
      this.loading=false
    },
  },
};
</script>

<style lang="less">
.searchresults {
  margin-top: 50px;
  .show-more {
    font-size: 12px;
    text-align: center;
    color: #999;
    margin: 10px 0;
  }
}
</style>