<template>
  <ul class="tabcontent">
    <li v-for="(item, index) in categories" :key="index" @click="navigateToBookList(item.name,active)">
      <img :src="icon[index].imgSrc" alt />
      {{item.name}}
    </li>
  </ul>
</template>

<script>
export default {
  props: ["categories","icon","active"],
  methods: {
    navigateToBookList:function (name,active) {
      this.$router.push({
        path:"/categoryList",
        query:{
          name,
          active
        }
      })
    }
  },
};
</script>

<style lang="less">
  .tabcontent{
    padding: 10px;
    li{
      float: left;
      width: 50%;
      height: 60px;
      line-height: 60px;
      img{
        vertical-align: middle;
      }
    }
  }
</style>