<template>
  <div class="maleHome">
    <HomeSwipe :images="images" />
    <HomeBookListA v-if="rankFantasy" :updata="updataFantasy" :count="count" :name="category[0]">火爆玄幻文</HomeBookListA>
    <HomeBookListB v-if="rankCity" :updata="updataCity" :count="count" :name="category[1]">都市热文</HomeBookListB>
    <HomeBookListC v-if="rankParanormal" :updata="updataParanormal" :count="count" :name="category[2]">灵异怪谈</HomeBookListC>
  </div>
</template>

<script>
import HomeSwipe from "@/components/HomeSwipe.vue";
import HomeBookListA from "@/components/HomeBookListA.vue";
import HomeBookListB from "@/components/HomeBookListB.vue";
import HomeBookListC from "@/components/HomeBookListC.vue";
// import { mapState } from "vuex";
export default {
  components: {
    HomeSwipe,
    HomeBookListA,
    HomeBookListB,
    HomeBookListC,
  },
  // computed: {
  //   ...mapState(["rankFantasy"]),
  // },
  data() {
    return {
      images: [
        "https://plf-new.zhuishushenqi.com/management/images/20201218/0d8dc8bbb17a4c9793706ab12d979697.jpg",
        "https://plf-new.zhuishushenqi.com/management/images/20201218/12ddcc817cdf49f787959bb53e21b584.png",
        "https://plf-new.zhuishushenqi.com/management/images/20201218/574d9d74adae46a7a75a3897414cf365.png",
      ],
      rankFantasy: null,
      rankCity: null,
      rankParanormal: null,
      count: 0,
      category: ["玄幻", "都市", "灵异"],
    };
  },
  created() {
    this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=male&type=hot&major=玄幻&minor=&start=1&limit=20"
      )
      .then((response) => {
        // this.$store.commit("updateRankFantasy", response.data.books);
        this.rankFantasy = response.data.books;
        // console.log(this.rankFantasy);
      });
    this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=male&type=hot&major=都市&minor=&start=1&limit=20"
      )
      .then((response) => {
        // this.$store.commit("updateRankCity", response.data.books);
        this.rankCity = response.data.books;
      });
    this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=male&type=hot&major=灵异&minor=&start=1&limit=20"
      )
      .then((response) => {
        // this.$store.commit("updateRankParanormal", response.data.books);
        this.rankParanormal = response.data.books;
        // console.log(response);
      });
  },
  computed: {
    updataFantasy: function () {
      return this.rankFantasy.slice(0, 5);
    },
    updataCity: function () {
      return this.rankCity.slice(0, 5);
    },
    updataParanormal: function () {
      return this.rankParanormal.slice(0, 4);
    },
  },
};
</script>

