<template>
  <div class="PublishingHome">
    <HomeBookListA
      v-if="youthRomance"
      :updata="youthRomance"
      :count="count"
      :name="category[0]"
    >青春言情</HomeBookListA>
    <HomeBookListB v-if="original" :updata="original" :count="count" :name="category[1]">外文原版</HomeBookListB>
    <HomeBookListC v-if="humanities" :updata="humanities" :count="count" :name="category[2]">人文社科</HomeBookListC>
  </div>
</template>

<script>
import HomeBookListA from "@/components/HomeBookListA.vue";
import HomeBookListB from "@/components/HomeBookListB.vue";
import HomeBookListC from "@/components/HomeBookListC.vue";

export default {
  components: {
    HomeBookListA,
    HomeBookListB,
    HomeBookListC,
  },
  data() {
    return {
      youthRomance: null,
      humanities: null,
      original: null,
      count: 2,
      category: ["青春言情", "人文社科", "外文原版"],
    };
  },
  created() {
    this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=press&type=hot&major=青春言情&minor=&start=1&limit=20"
      )
      .then((response) => {
        // this.$store.commit("updateRankParanormal", response.data.books);
        this.youthRomance = response.data.books;
        // console.log(this.youthRomance);
      });
    this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=press&type=hot&major=人文社科&minor=&start=1&limit=20"
      )
      .then((response) => {
        // this.$store.commit("updateRankParanormal", response.data.books);
        this.humanities = response.data.books;
        // console.log(this.humanities);
      });
    this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=press&type=hot&major=外文原版&minor=&start=1&limit=20"
      )
      .then((response) => {
        // this.$store.commit("updateRankParanormal", response.data.books);
        this.original = response.data.books;
        // console.log(this.original);
      });
  },
};
</script>

<style>
.PublishingHome {
  margin-top: 20px;
}
</style>