<template>
  <div class="hotsearchbooks">
    <div class="background"></div>
    <div class="hotbookscontent">
      <h3>热搜书籍</h3>
      <ul class="hotbookslist">
        <li v-for="(item, index) in currenthotwords" :key="index"  @click="updateWord(item.word)">
          <div class="serialnumber">{{index+1}}</div>
          {{item.word}}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ["currenthotwords"],
  methods: {
    updateWord:function(word){
      this.$store.commit("updateWord", word);
      // console.log(word);
    }
  },
};
</script>

<style lang="less">
.hotsearchbooks {
  position: relative;
  padding-top: 1px;
  margin-top: 45px;
  .background {
    width: 100vw;
    height: 15vh;
    z-index: -1;
    position: absolute;
    background: #f6f7fb;
  }
  .hotbookscontent {
    height: 315px;
    margin: 20px 15px;
    padding-top: 20px;
    background: white;
    box-shadow: 0px 0px 2px #ccc;
    border-radius: 25px;
    h3 {
      font-size: 14px;
      font-weight: bold;
      margin-left: 10px;
    }
    .hotbookslist {
      margin-top: 10px;
      margin-left: 20px;
      & > li {
        position: relative;
        line-height: 28px;
        // position: absolute;
        float: left;
        margin: 10px 0;
        width: 50%;
        padding-left: 30px;
        height: 28px;
        color: #999;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        word-break: break-all;
        text-overflow: ellipsis;
        overflow: hidden;
        .serialnumber {
          width: 20px;
          height: 20px;
          position: absolute;
          top: 0px;
          left: 0px;
          background: #dcdadb;
          border-radius: 17px;
          padding: 4px;
          /*弹性盒模型*/
          display: flex;
          /*主轴居中对齐*/
          justify-content: center;
          /*侧轴居中对齐*/
          align-items: center;
          color: white;
          box-sizing: content-box;
        }
        &:nth-of-type(1) {
          .serialnumber {
            background: #fd3250;
          }
        }
        &:nth-of-type(2) {
          .serialnumber {
            background: #fe941c;
          }
        }
        &:nth-of-type(3) {
          .serialnumber {
            background: #fbe139;
          }
        }
      }
    }
  }
}
</style>