<template>
  <div class="titleslot">
    <h3>
      <slot></slot>
    </h3>
    <div class="more" @click="navigateToBookList(name,count)">
      <em>查看更多</em>
      <i>></i>
    </div>
  </div>
</template>

<script>
export default {
  props: ["count", "name"],
  methods: {
    navigateToBookList: function (name, active) {
      this.$router.push({
        path: "/categoryList",
        query: {
          name,
          active,
        },
      });
    },
  },
};
</script>


<style lang="less">
.titleslot {
  padding-left: 15px;
  position: relative;
  h3 {
    font-weight: bold;
  }
  .more {
    position: absolute;
    right: 15px;
    top: 0;
    font-size: 13px;
    em {
      color: #46a9fc;
      font-style: inherit;
    }
    i {
      margin-left: 2px;
      font-style: inherit;
    }
  }
}
</style>