<template>
  <div class="femalehome">
    <HomeSwipe :images="images" />
    <HomeBookListA
      v-if="rankAncientRomance"
      :updata="updataAncientRomance"
      :count="count"
      :name="category[0]"
    >古代言情</HomeBookListA>
    <HomeBookListB
      v-if="rankModernRomance"
      :updata="updataModernRomance"
      :count="count"
      :name="category[1]"
    >现代言情</HomeBookListB>
    <HomeBookListA
      v-if="rankYouthCampus"
      :updata="updataYouthCampus"
      :count="count"
      :name="category[2]"
    >青春校园</HomeBookListA>
    <HomeBookListB v-if="rankWuXia" :updata="updatakWuXia" :count="count" :name="category[3]">武侠仙侠</HomeBookListB>
  </div>
</template>

<script>
import HomeSwipe from "@/components/HomeSwipe.vue";
import HomeBookListA from "@/components/HomeBookListA.vue";
import HomeBookListB from "@/components/HomeBookListB.vue";
export default {
  components: {
    HomeSwipe,
    HomeBookListB,
    HomeBookListA,
  },
  data() {
    return {
      images: [
        "https://plf-new.zhuishushenqi.com/management/images/20201204/997ab2ca67b0464683a70a38c7984676.png",
        "https://plf-new.zhuishushenqi.com/management/images/20201204/c48aa5482db346e9837df790c6cf6116.jpg",
        "https://plf-new.zhuishushenqi.com/management/images/20201127/2b8b5e6886ba472c9d6baea14de138b4.png",
      ],
      rankAncientRomance: null,
      rankModernRomance: null,
      rankYouthCampus: null,
      rankWuXia: null,
      count: 1,
      category: ["古代言情", "现代言情", "青春校园", "武侠仙侠"],
    };
  },
  created() {
    this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=female&type=hot&major=古代言情&minor=&start=1&limit=20"
      )
      .then((response) => {
        // this.$store.commit("updateRankFantasy", response.data.books);
        this.rankAncientRomance = response.data.books;
      });
    this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=female&type=hot&major=现代言情&minor=&start=1&limit=20"
      )
      .then((response) => {
        // this.$store.commit("updateRankFantasy", response.data.books);
        this.rankModernRomance = response.data.books;
      });
    this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=female&type=hot&major=青春校园&minor=&start=1&limit=20"
      )
      .then((response) => {
        // this.$store.commit("updateRankFantasy", response.data.books);
        this.rankYouthCampus = response.data.books;
      });
    this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=female&type=hot&major=武侠仙侠&minor=&start=1&limit=20"
        // "https://novel.kele8.cn/categories"
      )
      .then((response) => {
        // this.$store.commit("updateRankFantasy", response.data.books);
        this.rankWuXia = response.data.books;
        // console.log(response);
      });
  },
  computed: {
    updataAncientRomance: function () {
      return this.rankAncientRomance.slice(0, 5);
    },
    updataModernRomance: function () {
      return this.rankModernRomance.slice(0, 5);
    },
    updataYouthCampus: function () {
      return this.rankYouthCampus.slice(0, 5);
    },
    updatakWuXia: function () {
      return this.rankWuXia.slice(0, 5);
    },
  },
};
</script>

