<template>
  <div id="app">
    <!-- <AppNav /> -->
    <keep-alive>
      <router-view />
    </keep-alive>
  </div>
</template>

<script>
// import AppNav from "@/components/AppNav.vue";
export default {
  components: {
    // AppNav,
  },
  created() {
    this.axios.get("https://novel.kele8.cn/categories").then((response) => {
      this.$store.commit("updateCategories", response.data);
    });
    this.judgeBookrack();
    this.judgeChapterRecord();
  },
  methods: {
    judgeBookrack() {
      var parse = localStorage.getItem("Bookrack");
      if (!parse) {
        localStorage.setItem("Bookrack", "[]");
      }
    },
    judgeChapterRecord() {
      var chapterRecordParse = localStorage.getItem("ChapterRecord");
      if (!chapterRecordParse) {
        localStorage.setItem("ChapterRecord", "[]");
      }
    },
  },
};
</script>
