<template>
  <div class="classA">
    <TitleSlot :count="count" :name="name">
      <slot></slot>
    </TitleSlot>
    <BookList :first="first[0]" />
    <div class="content">
      <div
        class="listBox"
        v-for="(item, index) in rank"
        :key="index"
        @click="navigateToBookList(item._id)"
      >
        <div class="cover">
          <img :src="'http://statics.zhuishushenqi.com'+item.cover" alt />
        </div>
        <h2>{{item.title}}</h2>
        <h4>{{item.author}}</h4>
      </div>
    </div>
  </div>
</template>

<script>
import TitleSlot from "@/components/TitleSlot.vue";
import BookList from "@/components/BookList.vue";
export default {
  props: ["updata","count","name"],
  components: {
    TitleSlot,
    BookList,
  },
  computed: {
    first: function () {
      return this.updata.slice(0, 1);
    },
    rank: function () {
      return this.updata.slice(1, 5);
    },
  },
  methods: {
    navigateToBookList: function (id) {
      this.$router.push({
        path: "/detail",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style lang="less" scoped>
.classA {
  margin-bottom: 25px;
  .content {
    display: flex;
    margin-top: 15px;
    margin-left: 15px;
    margin-right: 10px;
    width: 100%;
    // overflow: hidden;
    .listBox {
      width: 68px;
      margin-right: 6%;
      // text-align: center;
      flex: 1;
      .cover {
        width: 68px;
        height: 88px;
        border-radius: 5px;
        overflow: hidden;
        img {
          width: 68px;
          height: 88px;
        }
      }
      h2 {
        width: 80px;
        font-size: 13px;
        color: black;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        word-break: break-all;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      h4 {
        font-size: 12px;
        color: #999;
      }
    }
  }
}
</style>