<template>
  <div class="RecommendedHome">
    <RecommendedSwiper v-if="popularityRank" :currentPopularityRank="currentPopularityRank" />
    <HomeBookListD v-if="hotSellRank" :currentHotSellRank="currentHotSellRank">推荐热销榜单</HomeBookListD>
    <HomeBookListA
      v-if="bestSellingRank"
      :updata="currentBestSellingRank"
      :count="count"
      :name="category[1]"
    >畅销榜</HomeBookListA>
    <HomeBookListB
      v-if="newBookRank"
      :updata="currentNewBookRank"
      :count="count"
      :name="category[1]"
    >新书榜</HomeBookListB>
    <HomeBookListC
      v-if="theEndRank"
      :updata="currentTheEndRank"
      :count="count"
      :name="category[1]"
    >完结榜</HomeBookListC>
  </div>
</template>

<script>
import RecommendedSwiper from "@/components/RecommendedSwiper.vue";
import HomeBookListD from "@/components/HomeBookListD.vue";
import HomeBookListA from "@/components/HomeBookListA.vue";
import HomeBookListB from "@/components/HomeBookListB.vue";
import HomeBookListC from "@/components/HomeBookListC.vue";

export default {
  components: {
    RecommendedSwiper,
    HomeBookListD,
    HomeBookListA,
    HomeBookListB,
    HomeBookListC,
  },
  data() {
    return {
      popularityRank: null,
      hotSellRank: null,
      bestSellingRank: null,
      newBookRank: null,
      theEndRank: null,
      popularityRankIndex: 0,
      hotSellRankIndex: 0,
      count: 3,
      category: [
        { name: "畅销榜", id: "5a39ca20fc84c2b8ef82c9ed" },
        { name: "畅销榜", id: "5a39ca3ffc84c2b8ef82da82" },
        { name: "畅销榜", id: "5a39ca59fc84c2b8ef82e96c" },
        // instanceof  Object
      ],
    };
  },
  created() {
    // this.axios.get("https://novel.kele8.cn/rank-category").then((response) => {
    //   console.log(response);
    // });
    //人气榜
    this.axios
      .get("https://novel.kele8.cn/rank/5a322ef4fc84c2b8efaa8335")
      .then((response) => {
        // this.$store.commit("updatePopularityRank", response);
        this.popularityRank = response.data.ranking.books;
      });
    this.axios
      .get("https://novel.kele8.cn/rank/5a68296bfc84c2b8ef9efdb0")
      .then((response) => {
        // this.$store.commit("updateHotSellRank", response);
        this.hotSellRank = response.data.ranking.books;
      });
    //畅销榜
    this.axios
      .get("https://novel.kele8.cn/rank/5a39ca20fc84c2b8ef82c9ed")
      .then((response) => {
        // this.$store.commit("updateHotSellRank", response);
        this.bestSellingRank = response.data.ranking.books;
      });
    //新书榜
    this.axios
      .get("https://novel.kele8.cn/rank/5a39ca3ffc84c2b8ef82da82")
      .then((response) => {
        // this.$store.commit("updateHotSellRank", response);
        this.newBookRank = response.data.ranking.books;
      });
    //完结榜
    this.axios
      .get("https://novel.kele8.cn/rank/5a39ca59fc84c2b8ef82e96c")
      .then((response) => {
        // this.$store.commit("updateHotSellRank", response);
        this.theEndRank = response.data.ranking.books;
      });
  },
  computed: {
    currentPopularityRank: function () {
      return this.popularityRank.slice(
        this.popularityRankIndex * 8,
        (this.popularityRankIndex + 1) * 8
      );
    },
    currentHotSellRank: function () {
      return this.hotSellRank.slice(
        this.hotSellRankIndex * 10,
        (this.hotSellRankIndex + 1) * 10
      );
    },
    currentBestSellingRank: function () {
      return this.bestSellingRank.slice(0, 5);
    },
    currentNewBookRank: function () {
      return this.newBookRank.slice(0, 5);
    },
    currentTheEndRank: function () {
      return this.theEndRank.slice(0, 4);
    },
  },
  // activated() {
  //   this.popularityRankIndex =
  //     this.popularityRankIndex >= 6 ? 0 : this.popularityRankIndex + 1;
  //   this.hotSellRankIndex =
  //     this.hotSellRankIndex >= 4 ? 0 : this.hotSellRankIndex + 1;
  // },
};
</script>

